@import url(https://fonts.googleapis.com/css?family=Nunito:400,600,700);
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0px;
  background: #eaedf2;
  font-family: 'Nunito', sans-serif;
}

*{
  box-sizing: border-box;
}

*:before,
*:after {
box-sizing: border-box;
}

.weather-item__container {
  display: flex;
  height: 60px;
}

.container__border {
  border-bottom: 2px solid #eaedf2;
}
.city__container {
  display: flex; 
  background: #ffffff; 
  width: 100%; 
  margin-bottom: 30px;
}

.item__container {
  display: flex; 
  position: relative;
  flex-direction: row; 
  justify-content: flex-end; 
  width: 100%;
}

.city__title {
  display: inline-block;
  font-weight: 600; 
  font-size: 2rem; 
  margin: 7px; 
  margin-left: 0;
  padding-left: 20px;
}

.map__container {
  position: absolute; 
  height: 400px; 
  width: 88%; 
  bottom: 0; 
}

@media only screen and (max-width: 750px) {
  .city__container {
    flex-direction: column;
  }
  .item__container {
    justify-content: flex-start;
    flex-direction: column;
  }
  .map__container {
    position: relative;
    width: 100%
  }
}
